@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

html {
	-ms-scroll-snap-type: y mandatory;
	    scroll-snap-type: y mandatory;
}

.app {
	display: flex;
	flex-direction: column;
	place-items: center;
	height: 100vh;
	background-color: black;
}

.app__top {
	margin-top: 20px;
}

.app__top>h1 {
	text-align: center;
	color: white;
}

.app__logo {
	height: 12vh;
}

.app__videos {
	margin-top: 20px;
	position: relative;
	height: 90vh;
	background-color: white;
	width: 90%;
	border-radius: 15px;
	max-width: 320px;
	max-height: 1200px;
	overflow: scroll;
	-ms-scroll-snap-type: y mandatory;
	    scroll-snap-type: y mandatory;
}

.app__videos::-webkit-scrollbar {
	display: none;
}

.app__videos {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}

.videoCard {
    position: relative;
    border: 1px solid black;
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
}

.video__player {
    object-fit: fill;
    width: 100%;
    height: 100%;
}
.videoFooter {
    position: relative;
    bottom: 70px;
}

.videoFooter__text {
    position: absolute;
    bottom: 0;
    color: white;
    display: flex;
    margin-bottom: 20px;
    margin-left: 10px;
}

.videoFooter__text img {
    height: 35px;
    width: 35px;
    border-radius: 50%;
}

.videoFooter__text h3 {
    padding-top: 3px;
    font-size: 0.9rem;
    margin-left: 5px;
}


.videoFooter__text h3 button {
    color: white;
    font-weight: 900;
    text-transform: inherit;
    margin-bottom: 2px;
}

.videoFooter__ticker {
    display: flex;
    margin: 0;
    padding: 0;
}

.videoFooter__ticker .ticker {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 70%;
    margin-left: 40px;
}

.videoFooter__ticker .videoFooter__icon {
    position: absolute;
    left: 15px;
    height: 20px;
    width: 20px;
    bottom: -3px;
    color: white;
}

.videoFooter__ticker h1 {
    font-size: 12px;
    color: white;
}

.videoFooter__actions {
    display: flex;
    position: absolute;
    width: 95%;
    justify-content: space-between;
    color: white;
    margin-left: 7px;
}

.videoFooter__actionLeft .MuiSvgIcon-root {
    padding: 10px 6px;
}

.videoFooter__actionRight {
    display: flex;
    padding: 0 10px;
    margin-bottom: 8px;
}
.videoFooter__actionRight .MuiSvgIcon-root {
    font-size: smaller;
}
.videoFooter__stat {
    display: flex;
    align-items: center;
    margin-right: 10px;
}

.videoFooter__stat p {
    margin-left: 3px;
}
.videoHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    width: 100%;
    color: white;
}

.videoHeader > * {
    padding: 15px;
}
